.App {
  text-align: center;
}

.center{
  margin: auto;
  width: 400px;
  height: 400px;
}

.colorSequence-grid{
  display: flex ;
  justify-content: center;
  margin-top: 10px;
}

.colorSequence .front{
  border-radius: 100px;
}

.colorSequence .front{
  transform: rotateY(90deg);
  position: absolute;
}

.flipped .front{
  transform: rotateY(0deg);
}

.colorHidden-grid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
}

.colorHidden-grid > div{
  padding: 10px
}

